body {
  width: 100vw;
  height: 100vh;
  background: #1976D2 url("../img/bg.jpg") center center no-repeat fixed;
  background-size: cover;
  overflow-x: hidden; }

body.platform {
  background: #1976D2 url("../img/bg-platform.jpg") left center no-repeat fixed;
  width: 100vw;
  height: 100vh;
  background-size: cover;
  overflow-x: hidden; }

#header {
  background-color: rgba(0, 0, 0, 0.75);
  height: 3em;
  position: fixed;
  width: 100%;
  z-index: 9999; }

@media screen and (min-width: 768px) {
  .toggle-menu {
    display: none; } }

#menu {
  width: 70vw;
  background-color: rgba(0, 0, 0, 0.75);
  height: calc(100vh - 3em);
  position: absolute;
  left: -70vw;
  transition: left ease .4s;
  z-index: 9999; }
  #menu.open {
    left: 0; }
  @media screen and (min-width: 768px) {
    #menu {
      width: 100%;
      height: auto;
      background-color: transparent;
      left: 0; }
      #menu .menu {
        height: 3em; }
        #menu .menu__item {
          display: inline-block; }
          #menu .menu__item:hover {
            box-shadow: 0 3em #1976D2 inset; } }
  @media screen and (orientation: landscape) {
    #menu .menu__link {
      font-size: 0.9em; } }

#toggle-menu {
  color: #fff;
  height: 100%;
  cursor: pointer; }
  @media screen and (min-width: 768px) {
    #toggle-menu {
      display: none; } }

.menu {
  list-style: none;
  margin: 0; }
  .menu__item {
    padding: 0;
    transition: box-shadow ease .4s;
    box-shadow: 0 0 #1976D2 inset; }
    .menu__item:hover {
      box-shadow: 70vw 0 #1976D2 inset; }
    .menu__item:last-child {
      background-color: #ffa000; }
  .menu__link {
    color: #fff;
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    line-height: 2.5;
    display: block;
    padding: 0 30px; }

#banner {
  box-sizing: border-box;
  padding: 3em 0 0;
  height: 100vh; }

.container {
  height: 100%;
  color: #fff; }

.logo {
  outline: 2px solid #fff;
  height: 30%; }
  .logo__title {
    font-size: 1.5em; }
  .logo__subtitle {
    color: #fff;
    font-size: 1.1em; }
  @media screen and (orientation: landscape) {
    .logo__title {
      font-size: 2em; } }
  @media screen and (min-width: 768px) {
    .logo {
      width: auto;
      height: 35%; } }
  @media screen and (min-width: 1024px) {
    .logo {
      height: 35%; }
